// 

const JUST_NOW = 3000; //3s
 const IN_SECOND = 1000 * 60; //
 const IN_MINUTE = 1000 * 60 * 60; //
 const IN_HOUR = 1000 * 60 * 60 * 12; //12
 const IN_DAY = 1000 * 60 * 60 * 24 * 1; //1
 const IN_MONTH = 1000 * 60 * 60 * 24 * 30; //1
module.exports={
    time(options){
        var date = new Date(options)
        var y = date.getFullYear()
        var m = date.getMonth() + 1
        m = m < 10 ? ('0' + m) : m
        var d = date.getDate()
        d = d < 10 ? ('0' + d) : d
        var h = date.getHours()
        h = h < 10 ? ('0' + h) : h
        var minute = date.getMinutes()
        var second = date.getSeconds()
        minute = minute < 10 ? ('0' + minute) : minute
        second = second < 10 ? ('0' + second) : second
        return y + '-' + m + '-' + d + ' ' + h + ':'+ minute+':'+second     
    },
	relativtime(time){
		  let localTime = new Date(); //
		  // console.log(localTime) //Mon Mar 22 2021 10:20:43 GMT+0800 ()
		  let createTime = new Date(time) //
		  // let createTime =new Date(1616309398000) //
		  // console.log(createTime) //
		  let diff = localTime - createTime;
		  // console.log(diff)
		  if (diff <= JUST_NOW)
		    return '';
		  else if (diff <= IN_SECOND)
		    return "1";
		  else if (diff <= IN_MINUTE)
		    return parseInt(diff / IN_SECOND) + '';
		  else if (diff <= IN_MINUTE)
		    return parseInt(diff / IN_MINUTE) + '';
		  else if (diff <= IN_HOUR * 2) {//<=24h
		    const list = createTime.toString().split(" ");
			// console.log(list)
		    const lastIndex = list[4].lastIndexOf(":") //-1
			// console.log(lastIndex)
		    const realtime = list[4].toString().substring(0, lastIndex);
			// console.log(realtime)
			const nowTime=localTime.toString().split(" ");
			if(list[2]<nowTime[2]){
				return ''+realtime;
			}else{
				return realtime;
			}
		  } else if (diff < IN_DAY * 7) {//7
		    if (diff < IN_DAY) {//<1
		      return parseInt(diff / IN_HOUR) + '';
		    }
			// else{
			// 	return parseInt(diff / IN_DAY) + '';
			// }
			const t = createTime.toString().slice(0, 3);
			switch (t) {
			  case "Mon":
				return '';
			  case "Tue":
				return '';
			  case "Wed":
				return '';
			  case "Thu":
				return '';
			  case "Fri":
				return '';
			  case "Sat":
				return '';
			  case "Sun":
				return '';
			}
		  } else if (diff < IN_MONTH*24) {//
				const list = createTime.toString().split(" ");
				const month = list[1];
				var realtime = "";
				if (diff < IN_MONTH) {//
					realtime += getNumberMonth(month)+"";//
				} else {
					realtime = list[3] + "" + getNumberMonth(month)+"";//-
				}
				return realtime + list[2] +""; //
		  }else{
			  const list = createTime.toString().split(" ");
			  const month = list[1];
			  var realtime = list[3] + "-" + getNumberMonth(month);//-
			  return realtime + "-" + list[2]; //
		  };
		function getNumberMonth(month){
			 switch (month) {
			    case "Jan":
			      return "1";
			    case "Feb":
			      return "2";
			    case "Mar":
			      return "3";
			    case "Apr":
			      return "4";
			    case "May":
			      return "5";
			    case "June":
			      return "6";
			    case "July":
			      return "7";
			    case "Aug":
			      return "8";
			    case "Sept":
			      return "9";
			    case "Oct":
			      return "10";
			    case "Nov":
			      return "11";
			    case "Dec":
			      return "12";
			  }
		};
		function isThisWeek(time) {
			  // 
			  var timestamp = Date.parse(new Date());
			  var serverDate = new Date(timestamp);
			  //
			  var sundayTiem = timestamp + ((7 - serverDate.getDay())* 24 * 60 * 60 * 1000)
			  var SundayData = new Date(sundayTiem);
			  //
			  var tomorrowY = SundayData.getFullYear();
			  //
			  var tomorrowM = (SundayData.getMonth() + 1 < 10 ? '0' + (SundayData.getMonth() + 1) : SundayData.getMonth() + 1);
			  //
			  var tomorrowD = SundayData.getDate() < 10 ? '0' + SundayData.getDate() : SundayData.getDate();
			  // 
			  var mondayTime = timestamp - ((serverDate.getDay()-1) * 24 * 60 * 60 * 1000)
			  var mondayData = new Date(mondayTime);
			  //
			  var mondayY = mondayData.getFullYear();
			  //
			  var mondayM = (mondayData.getMonth() + 1 < 10 ? '0' + (mondayData.getMonth() + 1) : mondayData.getMonth() + 1);
			  //
			  var mondayD = mondayData.getDate() < 10 ? '0' + mondayData.getDate() : mondayData.getDate();
			  // 
			  var currentData = new Date(time);
				console.log(currentData)
			  //
			  var currentY = currentData.getFullYear();
			  //
			  var currentM = (currentData.getMonth() + 1 < 10 ? '0' + (currentData.getMonth() + 1) : currentData.getMonth() + 1);
			  //
			  var currentD = currentData.getDate() < 10 ? '0' + currentData.getDate() : currentData.getDate();
			  //
			  var currenH = currentData.getHours();
			  //
			  var currenM = currentData.getMinutes();
			  var sundayDay = tomorrowY + tomorrowM + tomorrowD; //
			  var mondayDay = mondayY + mondayM + mondayD;//
			  var currentDay = currentY + currentM + currentD;//
			  parseInt(currentDay);
			  parseInt(mondayDay);
			  if (parseInt(currentDay) >= parseInt(mondayDay)) {
					if (parseInt(currentDay) <= parseInt(sundayDay)) {
						return true;
					}
					else {//
			  	  return false;
					}
			  }
				else { //
			   return false;
			  }
		}
	},
}