import {myRequest} from '../../request.js'
import {publicdown} from './publicdown.js'
module.exports={
	checkUpdate(){
		plus.runtime.getProperty(plus.runtime.appid,(infor)=>{
			console.log(infor.version); 
			myRequest({
				url:'/App/getDoctorAppLatestEdition',
			}).then(res=>{
				console.log(res)
				if(res.data.code==0){
					var type=res.data.object.updateType//1 2
					if(res.data.object.updateType=='1'){
						var url=res.data.object.latestDoctorPkgDownloadUrl
					}else{
						var url=res.data.object.updateUrl//
					}
					 console.log(type,url)
					 console.log(res.data.object.codeVersion)
					 console.log(infor.version)
					 if(infor.version<res.data.object.codeVersion){
						uni.showModal({
							title:res.data.object.versionTitle||'',
							content:res.data.object.versionMessage||'',
							cancelText:'',
							confirmText:'',
							success(e){
								console.log(e)
								if(e.confirm){//	
								  uni.getSystemInfo({
									success:(res) => {
										console.log(res)
										//  
										if (res.platform == "android") {
											publicdown(type,url)	
										}else if(res.platform == "ios"){
											// 
											plus.runtime.openURL(url, function(res) {
											   console.log(res);   
											});
										}
									}
								  })
								}
							},
							fail(e){
								console.log(e)
							}
						})
					 }
				}
			}).catch(err=>{
				console.log(err)
			})
		})
	},
	update(type,url){
		uni.getSystemInfo({
			success:(res) => {
				console.log(res)
				//  
				if (res.platform == "android") {
					publicdown(type,url)	
				}else if(res.platform == "ios"){
					// 
					plus.runtime.openURL(url, function(res) {
					   console.log(res);   
					});
				}
			}
		})
	},
	
}