var request=require('../../request.js')
// 
export const getOrderList=params=>{
	console.log(params)
	return request.myRequest({
		url:'/omsOrder/queryOrderListForPage',
		data:params
	})
}  
// ,openid,
export const getOpenid=params=>{
	console.log(params)
	return request.myRequest({
		url:'/doctor/bindDoctorByOpenId',
		data:params,
		method:'POST'
	})
}
