var request=require('../../connect.js')
var webrequest=require('../../request.js') //8083
// 
export const sendMessage=params=>{
	console.log(params)
	return request.myRequest({
		url:'/message/sendPriMsg',
		data:params,
		method:'POST',
		header:'application/x-www-form-urlencoded'
	})
}  
