module.exports={
	checkLogin(url,type){//backpagebacktype
		console.log(url)
		console.log(type)
		var token=uni.getStorageSync('token')
		if(!token){
			uni.showModal({
				title:'',
				content:'',
				confirmText:'',
				success(e){   
					console.log(e)
					if(e.confirm){//
						uni.navigateTo({//1switchTab
							url:'/pages/login/tellogin?pages='+url+'&pagetype='+type,
							animationType:'pop-in'
						})
					}	
				},
			})
			return false;
		}
		return [token]
	}
}