// 
import {socketurl} from '@/utill/baseUrl/baseurl.js'
import store from '@/store/index.js'
import {clientId} from '@/utill/api/clientid/clientid.js'
module.exports={
    loginTip(options,url,type){
		// 
		console.log(options)
		console.log(url)//
		console.log(type) //1switchTab2 navigateTo
		uni.setStorageSync('auditstatus',options.data.object.auditStatus)//
		uni.setStorageSync('token', options.data.object.token);//token
		uni.setStorageSync('doctorId', options.data.object.doctorId);//ID
		store.dispatch('WEBSOCKET_INIT',options.data.object.doctorId) //socket













		switch (options.data.object.auditStatus){
			case 'NP':
				uni.navigateTo({//
					url:'/pages/PersonalInfor/auditInfor',
					animationType:'pop-in'
				})
			break;
			case 'ZC'://
				// 
				uni.navigateTo({
					url:'/pages/PersonalInfor/auditInfor',
					animationType:'pop-in'
				})
			break;
			default:
				switch (type){
					case '1':{
						uni.switchTab({
							url:url,//
							animationType:'pop-in'
						})
					}
					break;
					case '2':{//
						uni.navigateTo({
							url:url,
							animationType:'pop-in'
						})
					}
					break;
					default://storeundefined
						uni.switchTab({
							url:'/pages/index/index',
							animationType:'pop-in'
						})
					break;
				}
			break;
		}
    },
}