module.exports={
	publicdown(type,url){
		console.log(type)
		console.log(url)
		if(type=='2'){//
			var showLoading = plus.nativeUI.showWaiting(",...");
			const downloadTasks =
				uni.downloadFile({
					url: url,
					success: (res) => {
						console.log(res)
						if(res.statusCode===200){
							var filePath = res.tempFilePath;
							 // wgtwgtu false,truefalse
							plus.runtime.install(filePath,{force: true,},(e) => {
								showLoading.setTitle("...");
								plus.runtime.restart(); //
								plus.nativeUI.closeWaiting();
							},(e) => {
								showLoading.setTitle("");
								setTimeout(()=>{
									  plus.nativeUI.closeWaiting();
								},1000)
							})
						}
						
					},
					fail:(err)=>{
						showLoading.setTitle("");
						setTimeout(()=>{
							  plus.nativeUI.closeWaiting();
						},1000)
					}
				})
				downloadTasks.onProgressUpdate((res) => {
					console.log(res)
					showLoading.setTitle("" + res.progress + "%");
					if(res.progress==100){
						showLoading.setTitle("");
					}
				})
		}else{//
			var showLoading = plus.nativeUI.showWaiting(",...");
			const downloadTask =
				uni.downloadFile({
					url: url,
					success: (res) => {
						console.log(res)
						var filePath = res.tempFilePath;
						plus.runtime.install(filePath,{force: false,},(e) => {
							showLoading.setTitle("...");
							setTimeout(()=>{
								  plus.nativeUI.closeWaiting();
							},1000)
						},(e) => {
							showLoading.setTitle("");
							setTimeout(()=>{
								  plus.nativeUI.closeWaiting();
							},1000)
						})
					},
					fail:(err)=>{
						showLoading.setTitle("");
						setTimeout(()=>{
							  plus.nativeUI.closeWaiting();
						},1000)
					}
				})
				downloadTask.onProgressUpdate((res) => {
					console.log(res)
					showLoading.setTitle("" + res.progress + "%");
					if(res.progress==100){
						console.log(res.progress)
						showLoading.setTitle("");
						plus.nativeUI.closeWaiting();
					}
				})
		}
	}
}