import {requestUrl} from './baseUrl/baseurl.js'
const BASE_URL=requestUrl
let token=uni.getStorageSync('token');
export const uploadRequest = (options) => {//options
		  console.log(options)
		  return new Promise((resolve,reject)=>{
		  	// postget
		  	// if(options.method==='GET'){
				// request}
		  	uni.uploadFile({
		  		url:BASE_URL+options.url,
		  		method:options.method || 'POST',//GET	
		  		// data:options.data||{},//
				filePath: options.filePath,
				name: options.name || 'file',//filefiles
		  		// token
				header:{
		  			// 'Content-Type':'application/json;charset=UTF-8',//// POST  header['content-type']  application/json  JSON 
		  			// 'Content-Type':'application/x-www-form-urlencoded',// query string
		  			"token":uni.getStorageSync('token')
		  		},
		  		withCredentials:true,//
		  		success:(res)=> {
		  			//res
		  			// if(res.data.code!==0){
		  			// 	return uni.showToast({
		  			// 		title:res.data.msg,
		  			// 		icon:"none"
		  			// 	})
		  			// }
		  			resolve(res)//
		  		},
		  		fail: (err) => {//
		  			uni.showToast({
		  				title:'',
						icon:"none"
		  			})
		  			reject(err)//
		  		},
		  		complete: (response) => {
		  			// 
		  			// console.log(response)
		  			if(response.statusCode==401){//
		  				console.log('')
						uni.showToast({
							title:'',
							icon:none
						})
		  				// uni.navigateTo({
		  				// 	url: '/pages/login/login' 
		  				// });
		  			}
		  		}
		  	})
		  })
	  // }
	  // else{
		 //  console.log('')
	  // }
}