import {update} from '@/utill/tools/update/checkUpdate.js'
module.exports = {
	push(){
		// click
		plus.push.addEventListener('click', function(message) {
			console.log('click')
			// plus.nativeUI.toast('click')
			console.log(message)
			console.log(message.payload) //
			var data=message.payload
			switch (data.messageType){//
				case 1:
					plus.runtime.getProperty(plus.runtime.appid,(infor)=>{
						if(infor.version<data.codeVersion){ //
							if(data.updateType=='1'){//
								var url=data.downloadUrl
							}else{//
								var url=data.updateUrl
							}
							update(data.updateType,url) //
						}else{
							uni.showToast({
								title:'',
								icon:'none'
							})
						}
					})
				break;
				case 2://
					uni.navigateTo({
						url:'/pages/news/newsDetail?id='+data.id
					})
				break;
				case 3://
					uni.navigateTo({
						url:'/pages/notice/noticeDetail?id='+data.id
					})
				break;
				case 4://
					uni.navigateTo({ //
						url:'/pages/PersonalInfor/PersonalInfor'
					})
				break;
				case 5://
					uni.navigateTo({
						url:'/pages/surgicalData/surgicalDetail?id='+data.id
					})
				break;
				case 6://
					uni.navigateTo({
						url:'/pages/publicModel/modelDetail?modelId='+data.id+'&classifyId='+data.modelCategory
					})
				break;
				default://
					uni.navigateTo({
						url:'/pages/shoyiiChat/index'
					})
				break;
			}
		});
		// receive
		plus.push.addEventListener('receive', function(message) {
			console.log('receive')
			// plus.nativeUI.toast('receive')
			console.log(message)
			const payload=message.payload
			const options={
				title:message.title,
			}
			// 
			// plus.push.createMessage(message.content,payload,options)
		});
	}
}