import Vue from 'vue'
import Vuex from 'vuex'
import {socketurl} from '@/utill/baseUrl/baseurl.js'
Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		//-------------------------------------
		hasLogin: false, //
		openid: null,
		// ----------------------------
		PagesUrl:'',//
		PagesType:"",//
		modelIds:'',
		appTheme:'AppWhite',//app
		modulebg:'ModuleWhite',//
		textcolor:'Textblack',//
		dataStatus:false,//logo
		connectStatus:false,//
		newNoticeStatus:false,//
		socketTask:null,//socket
		is_open_socket:false,//
		SocketState: {},
		SocketStateErr: {},
		socketData:'',
		timer:null,
	},
	mutations: {
		// 
		SOCKET_INIT(state,id){
			console.log(socketurl)
			console.log(id)
			state.socketTask=uni.connectSocket({ //
				url: socketurl+id,
				success:(e)=>{
					console.log(e)
					console.log('')
				},
				fail:(err)=>{
					console.log(err)
					console.log('')
				}
			});
			// 
			state.socketTask.onOpen((res) => {
				console.log("WebSocket...");
				state.is_open_socket = true;
				//  
				state.socketTask.onMessage((res) => {
					console.log("" + res.data);
					state.socketData=res.data







































				});
				state.timer=setInterval(()=>{ //10s
					 state.socketTask.send({
						data: 'head',
						success(e) {
							// console.log("");
						},
						fail(e) {
							console.log(e)
							console.log('')
							clearInterval(state.timer)
							state.is_open_socket = false;
							console.log(uni.getStorageSync('doctorId'))
							state.socketTask=uni.connectSocket({ //
								url: socketurl+uni.getStorageSync('doctorId'),
								success:(e)=>{
									console.log(e)
									console.log('')
									// state.is_open_socket = true;
								},
								fail:(err)=>{
									console.log(err)
									console.log('')
								}
							});
							console.log(state.socketTask)
						}
					}); 
				},10000)
			},(err)=>{
				console.log(err)
			})
		},
		// 
		SOCKET_SEND(state, data) {
			console.log("ws");
			console.log(data)
			state.socketTask.send({
				data: data,
				async success(e) {
					console.log("");
				},
			});
		},
		// setSocketState(that, info) {
		// 	that.SocketState = info;
		// },
		// setSocketStateErr(that, info) {
		// 	that.SocketStateErr = info;
		// },
		// socket
		SOCKET_CLOSE(state, data){
			state.socketTask.close({
				success(e){
					console.log('socekt')
				},fail(err){
					console.log(err)
				}
			})
		},
		hasLogin(state, provider) {
			console.log(provider)
			state.hasLogin = provider;
		},
		// 
		savePages(state,payload){
			console.log(state)
			console.log(payload)
			state.PagesUrl=payload.pages//
			state.PagesType=payload.pagetype//1 switchTab2navigateTo
		},
		saveModelIds(state,payload){
			state.modelIds= payload
			console.log(state.modelIds)
		},
		// app
		changeTheme(state,data){
			console.log(state)
			console.log(data)
			state.appTheme=data.appbg
			state.modulebg=data.moduleBg
			state.textcolor=data.Textcolor
		},
		// ()
		changeDataStatus(state,data){
			state.dataStatus=data
		},
		connectData(state,data){
			console.log(data)
			state.newNoticeStatus=data
		}
	},
    getters:{
        appTheme(state){
            return state.appTheme
        },
		moduleBg(state){
			return state.modulebg
		},
		textcolor(state){
			return state.textcolor
		},
		DataStatus(state){
			return state.dataStatus
		},
		connect(state){
			console.log(state.newNoticeStatus)
			return state.newNoticeStatus
		}
    },
	actions: {
		// 
		WEBSOCKET_INIT(
			{commit}, id) {
			commit('SOCKET_INIT', id)
		},
		// 
		WEBSOCKET_SEND({
			commit
		}, data) {
			commit('SOCKET_SEND', data)
		},
		// lazy loading openid
		getUserOpenId: async function ({
			commit,
			state
		}) {
			return await new Promise((resolve, reject) => {
				if (state.openid) {
					resolve(state.openid)
				} else {
					uni.login({
						success: (data) => {
							commit('login')
							setTimeout(function () { // openid
								const openid = '123456789'
								console.log('uni.request mock openid[' + openid + ']');
								commit('setOpenid', openid)
								resolve(openid)
							}, 1000)
						},
						fail: (err) => {
							console.log('uni.login ', err)
							reject(err)
						}
					})
				}
			})
		}
	}
})
export default store
